.container-project {
  margin-top: 200px;

  .containerProjectImage {
    .infoProjectSkills {
      background-color: #00c4cc;
      color: white;
      border-radius: 10px;
      margin-right: 10px;
      padding: 5px;
      position: relative;
      top: 10px;
      // overflow: hidden !important;
    }
    img {
      cursor: pointer;
      transition: all ease-in 0.5s;
      &:hover {
        transition: all ease-out 0.5s;
        transform: scale(0.9);
      }
      &:active {
        &:hover {
          transition: all ease-out 0.5s;
          transform: scale(0.9);
        }
      }
    }
  }
  .carousel {
    min-width: 60%;
    .carousel-inner {
      .carousel-item {
        height: 500px !important;
        background-position: center !important;
        background-repeat: no-repeat;
        background-size: contain;
        height: "500px" !important;
        cursor: pointer;
        &:nth-child(1) {
          background-image: url("../../images/social-net.png") !important;
          transition: all ease-in 0.5s;
          &:hover {
            transition: all ease-out 0.5s;
            transform: scale(0.9);
          }
        }
        &:nth-child(2) {
          background-image: url("../../images/cosmetic-web.png") !important;
          transition: all ease-in 0.5s;
          &:hover {
            transition: all ease-out 0.5s;
            transform: scale(0.9);
          }
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .container-project {
    .containerProjectImage {
      .card-body {
        div {
          display: flex !important;
          flex-flow: wrap !important;
          .infoProjectSkills {
            margin-top: 10px;
          }
        }
      }
      // .infoProjectSkills {
      //   display: flex;
      //   flex-flow: wrap;
      //   // overflow: hidden !important;
      // }
      // img {
      //   transition: all ease-in 0.5s;
      //   &:hover {
      //     transition: all ease-out 0.5s;
      //     transform: scale(0.9);
      //   }
      // }
    }
    .containerTitleProject {
      padding: 0px !important;
      margin: 0px !important;
    }
    .carousel {
      min-width: 100%;
    }
    .containerProjectImage {
      margin-top: 19px;
    }
  }
}
